/**
 * Editor Area with Terminal Component
 * 
 * Main editor with tabs, Monaco Editor, Terminal, and Run functionality
 */

import { useApp } from '../../context/AppContext';
import EditorTabs from './EditorTabs';
import MonacoEditor from './MonacoEditor';
import TerminalPanel from '../Terminal/TerminalPanel';
import { Zap, Upload, MessageSquare, Terminal, Play, Bug, FileCode } from 'lucide-react';
import { useState, useRef } from 'react';

// Backend URL
const BACKEND_URL = import.meta.env.VITE_BACKEND_URL || 'https://construct-eb7w.onrender.com';

function EditorAreaWithTerminal({ showTerminal, onToggleTerminal, onCloseTerminal }) {
    const { activeFile, openFiles, openFile, sendMessage } = useApp();
    const [isRunning, setIsRunning] = useState(false);
    const [runOutput, setRunOutput] = useState([]);
    const terminalRef = useRef(null);

    // Handle opening files
    const handleOpenFile = () => {
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = '.py,.js,.jsx,.ts,.tsx,.json,.md,.css,.html,.yaml,.yml,.txt';
        input.multiple = true;

        input.onchange = (e) => {
            const files = Array.from(e.target.files);
            files.forEach(file => {
                const reader = new FileReader();
                reader.onload = (event) => {
                    const content = event.target.result;
                    openFile({
                        id: `file-${Date.now()}-${file.name}`,
                        name: file.name,
                        path: `/${file.name}`,
                        language: getLanguageFromName(file.name),
                        content: content,
                    });
                };
                reader.readAsText(file);
            });
        };

        input.click();
    };

    const getLanguageFromName = (filename) => {
        const ext = filename.split('.').pop()?.toLowerCase();
        const langMap = {
            'py': 'python',
            'js': 'javascript',
            'jsx': 'javascript',
            'ts': 'typescript',
            'tsx': 'typescript',
            'json': 'json',
            'md': 'markdown',
            'css': 'css',
            'html': 'html',
        };
        return langMap[ext] || 'plaintext';
    };

    // Run current file
    const handleRunFile = async () => {
        if (!activeFile || isRunning) return;

        setIsRunning(true);
        setRunOutput([{ type: 'system', content: ` Running ${activeFile.name}...` }]);

        // Open terminal if not visible
        if (!showTerminal) {
            onToggleTerminal();
        }

        const code = activeFile.content;
        const language = activeFile.language;

        try {
            // Connect to backend WebSocket
            const wsUrl = BACKEND_URL.replace('https', 'wss').replace('http', 'ws') + '/api/v1/ws';
            const ws = new WebSocket(wsUrl);

            ws.onopen = () => {
                setRunOutput(prev => [...prev, { type: 'system', content: 'Connected to Construct AI...' }]);
                ws.send(`Execute this ${language} code and show the complete output. Run the code step by step and explain what happens:\n\`\`\`${language}\n${code}\n\`\`\``);
            };

            ws.onmessage = (event) => {
                try {
                    const data = JSON.parse(event.data);
                    if (data.type === 'token') {
                        setRunOutput(prev => [...prev, { type: 'output', content: data.content, sender: data.sender }]);
                    } else if (data.type === 'complete') {
                        setRunOutput(prev => [...prev, { type: 'system', content: ' Execution complete' }]);
                        setIsRunning(false);
                        ws.close();
                    } else if (data.type === 'error') {
                        setRunOutput(prev => [...prev, { type: 'error', content: `Error: ${data.error}` }]);
                        setIsRunning(false);
                        ws.close();
                    }
                } catch (e) {
                    setRunOutput(prev => [...prev, { type: 'output', content: event.data }]);
                }
            };

            ws.onerror = (error) => {
                console.error('WebSocket error:', error);
                setRunOutput(prev => [...prev, { type: 'error', content: 'Connection failed. Running locally...' }]);
                // Fallback: run locally for JavaScript
                if (language === 'javascript' || language === 'js') {
                    try {
                        const result = eval(code);
                        setRunOutput(prev => [...prev, { type: 'output', content: String(result) }]);
                    } catch (e) {
                        setRunOutput(prev => [...prev, { type: 'error', content: e.message }]);
                    }
                }
                setIsRunning(false);
            };

            ws.onclose = () => {
                setIsRunning(false);
            };

            // Timeout after 60 seconds
            setTimeout(() => {
                if (ws.readyState === WebSocket.OPEN) {
                    ws.close();
                    setRunOutput(prev => [...prev, { type: 'system', content: ' Execution timeout (60s)' }]);
                    setIsRunning(false);
                }
            }, 60000);

        } catch (error) {
            setRunOutput(prev => [...prev, { type: 'error', content: error.message }]);
            setIsRunning(false);
        }
    };

    // Review code with AI
    const handleReviewCode = () => {
        if (!activeFile) return;
        const code = activeFile.content;
        sendMessage(`Review this ${activeFile.language} code for bugs, security issues, and improvements:\n\`\`\`${activeFile.language}\n${code}\n\`\`\``);
    };

    // Welcome screen when no files are open
    if (!activeFile) {
        return (
            <div className="editor-area" style={{ display: 'flex', flexDirection: 'column' }}>
                <div className="editor-welcome" style={{ background: '#000000', flex: 1 }}>
                    {/* Logo */}
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: 12,
                        marginBottom: 8
                    }}>
                        <div style={{
                            width: 48,
                            height: 48,
                            background: 'linear-gradient(135deg, #333 0%, #444 100%)',
                            borderRadius: 12,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}>
                            <Zap size={28} color="#e5e5e5" />
                        </div>
                        <div className="welcome-logo" style={{
                            fontSize: 42,
                            fontWeight: 600,
                            color: '#e5e5e5',
                            background: 'none',
                            WebkitBackgroundClip: 'unset',
                            WebkitTextFillColor: 'unset',
                        }}>
                            Construct
                        </div>
                    </div>

                    <div className="welcome-subtitle" style={{
                        color: '#737373',
                        fontSize: 14,
                        marginBottom: 32
                    }}>
                        AI-Powered Code Editor
                    </div>

                    {/* Quick Actions */}
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        gap: 8,
                        width: 280
                    }}>
                        <button
                            onClick={handleOpenFile}
                            style={{
                                display: 'flex',
                                alignItems: 'center',
                                gap: 12,
                                padding: '14px 16px',
                                background: '#111111',
                                border: '1px solid #262626',
                                borderRadius: 8,
                                cursor: 'pointer',
                                color: '#e5e5e5',
                                fontSize: 14,
                                textAlign: 'left',
                                transition: 'all 0.15s ease',
                            }}
                            onMouseOver={(e) => e.currentTarget.style.background = '#1a1a1a'}
                            onMouseOut={(e) => e.currentTarget.style.background = '#111111'}
                        >
                            <Upload size={18} color="#737373" />
                            <div>
                                <div style={{ fontWeight: 500 }}>Open File</div>
                                <div style={{ fontSize: 12, color: '#737373', marginTop: 2 }}>
                                    Open .py, .js, .ts files to edit and run
                                </div>
                            </div>
                        </button>

                        <button
                            onClick={() => {
                                const chatInput = document.querySelector('.chat-input');
                                if (chatInput) chatInput.focus();
                            }}
                            style={{
                                display: 'flex',
                                alignItems: 'center',
                                gap: 12,
                                padding: '14px 16px',
                                background: '#111111',
                                border: '1px solid #262626',
                                borderRadius: 8,
                                cursor: 'pointer',
                                color: '#e5e5e5',
                                fontSize: 14,
                                textAlign: 'left',
                                transition: 'all 0.15s ease',
                            }}
                            onMouseOver={(e) => e.currentTarget.style.background = '#1a1a1a'}
                            onMouseOut={(e) => e.currentTarget.style.background = '#111111'}
                        >
                            <MessageSquare size={18} color="#737373" />
                            <div>
                                <div style={{ fontWeight: 500 }}>AI Chat</div>
                                <div style={{ fontSize: 12, color: '#737373', marginTop: 2 }}>
                                    Ask AI to review, debug, or write code
                                </div>
                            </div>
                        </button>

                        <button
                            onClick={onToggleTerminal}
                            style={{
                                display: 'flex',
                                alignItems: 'center',
                                gap: 12,
                                padding: '14px 16px',
                                background: '#111111',
                                border: '1px solid #262626',
                                borderRadius: 8,
                                cursor: 'pointer',
                                color: '#e5e5e5',
                                fontSize: 14,
                                textAlign: 'left',
                                transition: 'all 0.15s ease',
                            }}
                            onMouseOver={(e) => e.currentTarget.style.background = '#1a1a1a'}
                            onMouseOut={(e) => e.currentTarget.style.background = '#111111'}
                        >
                            <Terminal size={18} color="#737373" />
                            <div>
                                <div style={{ fontWeight: 500 }}>Terminal</div>
                                <div style={{ fontSize: 12, color: '#737373', marginTop: 2 }}>
                                    Run commands and execute code
                                </div>
                            </div>
                        </button>
                    </div>

                    {/* Instructions */}
                    <div style={{
                        marginTop: 40,
                        padding: 16,
                        background: '#111',
                        borderRadius: 8,
                        border: '1px solid #262626',
                        maxWidth: 400
                    }}>
                        <div style={{ color: '#10b981', fontWeight: 600, marginBottom: 8 }}>
                             How to Run Code:
                        </div>
                        <ol style={{ color: '#a3a3a3', fontSize: 13, lineHeight: 1.8, paddingLeft: 20, margin: 0 }}>
                            <li><strong style={{ color: '#e5e5e5' }}>Open a file</strong> (.py, .js, etc.)</li>
                            <li><strong style={{ color: '#e5e5e5' }}>Click "Run"</strong> button in the toolbar</li>
                            <li><strong style={{ color: '#e5e5e5' }}>View output</strong> in the terminal</li>
                            <li><strong style={{ color: '#e5e5e5' }}>AI reviews</strong> and executes your code</li>
                        </ol>
                    </div>

                    {/* Keyboard Shortcuts */}
                    <div style={{
                        marginTop: 24,
                        display: 'flex',
                        gap: 24,
                        color: '#525252',
                        fontSize: 12
                    }}>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 6 }}>
                            <kbd style={{
                                padding: '3px 6px',
                                background: '#1a1a1a',
                                borderRadius: 4,
                                border: '1px solid #262626',
                                fontFamily: 'var(--font-mono)',
                                fontSize: 11,
                            }}>F5</kbd>
                            <span>Run File</span>
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 6 }}>
                            <kbd style={{
                                padding: '3px 6px',
                                background: '#1a1a1a',
                                borderRadius: 4,
                                border: '1px solid #262626',
                                fontFamily: 'var(--font-mono)',
                                fontSize: 11,
                            }}>Ctrl+`</kbd>
                            <span>Terminal</span>
                        </div>
                        <div style={{ display: 'flex', alignItems: 'center', gap: 6 }}>
                            <kbd style={{
                                padding: '3px 6px',
                                background: '#1a1a1a',
                                borderRadius: 4,
                                border: '1px solid #262626',
                                fontFamily: 'var(--font-mono)',
                                fontSize: 11,
                            }}>Ctrl+L</kbd>
                            <span>AI Chat</span>
                        </div>
                    </div>
                </div>

                {/* Terminal Panel */}
                <TerminalPanel
                    isVisible={showTerminal}
                    onToggle={onToggleTerminal}
                    onClose={onCloseTerminal}
                    externalOutput={runOutput}
                />
            </div>
        );
    }

    return (
        <div className="editor-area" style={{ display: 'flex', flexDirection: 'column', position: 'relative' }}>
            {/* Toolbar with Run Button */}
            <div style={{
                height: 40,
                background: '#0a0a0a',
                borderBottom: '1px solid #1a1a1a',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
                padding: '0 12px',
            }}>
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                    <FileCode size={16} color="#737373" />
                    <span style={{ color: '#a3a3a3', fontSize: 13 }}>{activeFile.name}</span>
                    <span style={{
                        color: '#525252',
                        fontSize: 11,
                        padding: '2px 6px',
                        background: '#1a1a1a',
                        borderRadius: 4
                    }}>
                        {activeFile.language}
                    </span>
                </div>

                <div style={{ display: 'flex', gap: 8 }}>
                    {/* Run Button */}
                    <button
                        onClick={handleRunFile}
                        disabled={isRunning}
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: 6,
                            padding: '6px 12px',
                            background: isRunning ? '#1a1a1a' : '#10b981',
                            border: 'none',
                            borderRadius: 6,
                            color: isRunning ? '#737373' : '#fff',
                            fontSize: 12,
                            fontWeight: 500,
                            cursor: isRunning ? 'not-allowed' : 'pointer',
                            transition: 'all 0.15s ease',
                        }}
                        title="Run File (F5)"
                    >
                        {isRunning ? (
                            <>
                                <div className="animate-spin" style={{ width: 14, height: 14, border: '2px solid #525252', borderTopColor: '#a3a3a3', borderRadius: '50%' }} />
                                Running...
                            </>
                        ) : (
                            <>
                                <Play size={14} />
                                Run
                            </>
                        )}
                    </button>

                    {/* Review Button */}
                    <button
                        onClick={handleReviewCode}
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            gap: 6,
                            padding: '6px 12px',
                            background: '#1a1a1a',
                            border: '1px solid #262626',
                            borderRadius: 6,
                            color: '#a3a3a3',
                            fontSize: 12,
                            cursor: 'pointer',
                            transition: 'all 0.15s ease',
                        }}
                        onMouseOver={(e) => {
                            e.currentTarget.style.background = '#262626';
                            e.currentTarget.style.color = '#e5e5e5';
                        }}
                        onMouseOut={(e) => {
                            e.currentTarget.style.background = '#1a1a1a';
                            e.currentTarget.style.color = '#a3a3a3';
                        }}
                        title="Review with AI"
                    >
                        <Bug size={14} />
                        Review
                    </button>
                </div>
            </div>

            <EditorTabs />
            <div className="editor-container" style={{ flex: 1 }}>
                <MonacoEditor
                    file={activeFile}
                />
            </div>

            {/* Terminal Panel with Run Output */}
            <TerminalPanel
                isVisible={showTerminal}
                onToggle={onToggleTerminal}
                onClose={onCloseTerminal}
                externalOutput={runOutput}
            />

            {/* Terminal Toggle Button (fixed at bottom when terminal is hidden) */}
            {!showTerminal && (
                <button
                    onClick={onToggleTerminal}
                    style={{
                        position: 'absolute',
                        bottom: 16,
                        left: '50%',
                        transform: 'translateX(-50%)',
                        display: 'flex',
                        alignItems: 'center',
                        gap: 6,
                        padding: '8px 16px',
                        background: '#1a1a1a',
                        border: '1px solid #262626',
                        borderRadius: 6,
                        color: '#a3a3a3',
                        fontSize: 12,
                        cursor: 'pointer',
                        transition: 'all 0.15s ease',
                        zIndex: 10,
                    }}
                    onMouseOver={(e) => {
                        e.currentTarget.style.background = '#262626';
                        e.currentTarget.style.color = '#e5e5e5';
                    }}
                    onMouseOut={(e) => {
                        e.currentTarget.style.background = '#1a1a1a';
                        e.currentTarget.style.color = '#a3a3a3';
                    }}
                    title="Toggle Terminal (Ctrl+`)"
                >
                    <Terminal size={14} />
                    Terminal
                </button>
            )}
        </div>
    );
}

export default EditorAreaWithTerminal;
