/**
 * Chat History Component
 * 
 * List of previous chat sessions
 */

import { useApp } from '../../context/AppContext';
import { MessageSquare, Plus, Trash2 } from 'lucide-react';

function ChatHistory() {
    const { sessions, sessionId, newChat, messages } = useApp();

    // Create mock sessions based on current messages
    const mockSessions = [
        {
            id: 'current',
            title: 'Current Session',
            messageCount: messages.length,
            timestamp: new Date().toISOString(),
        },
        {
            id: 'session-1',
            title: 'Code Review: auth.py',
            messageCount: 8,
            timestamp: '2024-12-10T14:30:00Z',
        },
        {
            id: 'session-2',
            title: 'Debugging WebSocket',
            messageCount: 12,
            timestamp: '2024-12-09T10:15:00Z',
        },
        {
            id: 'session-3',
            title: 'API Optimization',
            messageCount: 5,
            timestamp: '2024-12-08T16:45:00Z',
        },
    ];

    const formatDate = (isoString) => {
        const date = new Date(isoString);
        const now = new Date();
        const diff = now - date;

        if (diff < 86400000) {
            return 'Today';
        } else if (diff < 172800000) {
            return 'Yesterday';
        } else {
            return date.toLocaleDateString('en-US', {
                month: 'short',
                day: 'numeric'
            });
        }
    };

    return (
        <div>
            {/* New Chat Button */}
            <button
                className="btn btn-primary"
                style={{
                    width: '100%',
                    marginBottom: '12px',
                    justifyContent: 'center'
                }}
                onClick={newChat}
            >
                <Plus size={16} />
                New Chat
            </button>

            {/* Session List */}
            <div className="session-list">
                {mockSessions.map(session => (
                    <div
                        key={session.id}
                        className={`session-item ${session.id === 'current' ? 'active' : ''}`}
                    >
                        <div className="session-item-title">
                            <MessageSquare size={14} style={{ marginRight: '6px', opacity: 0.7 }} />
                            {session.title}
                        </div>
                        <div className="session-item-meta">
                            {session.messageCount} messages  {formatDate(session.timestamp)}
                        </div>
                    </div>
                ))}
            </div>

            {mockSessions.length === 0 && (
                <div className="empty-state">
                    <MessageSquare className="empty-state-icon" />
                    <div className="empty-state-title">No Conversations</div>
                    <div className="empty-state-description">
                        Start a new chat to begin code review
                    </div>
                </div>
            )}
        </div>
    );
}

export default ChatHistory;
