/**
 * Status Bar Component
 * 
 * Bottom bar showing connection status, file info, and terminal toggle
 */

import { useApp } from '../../context/AppContext';
import {
    Wifi,
    WifiOff,
    GitBranch,
    Bell,
    Check,
    AlertCircle,
    Terminal,
    X,
    AlertTriangle
} from 'lucide-react';

function StatusBar({ showTerminal, onToggleTerminal }) {
    const { connectionStatus, activeFile, isStreaming, currentAgent } = useApp();

    const getConnectionIcon = () => {
        switch (connectionStatus) {
            case 'connected':
                return <div className="status-dot connected" />;
            case 'connecting':
                return <div className="status-dot connecting" />;
            default:
                return <div className="status-dot disconnected" />;
        }
    };

    const getConnectionText = () => {
        switch (connectionStatus) {
            case 'connected':
                return 'Connected to AI Backend';
            case 'connecting':
                return 'Connecting...';
            default:
                return 'Disconnected';
        }
    };

    return (
        <div className="status-bar">
            <div className="status-bar-left">
                {/* Connection Status */}
                <div className="status-item" title={getConnectionText()}>
                    {getConnectionIcon()}
                    <span>{connectionStatus === 'connected' ? 'AI Ready' : connectionStatus}</span>
                </div>

                {/* Streaming Status */}
                {isStreaming && currentAgent && (
                    <div className="status-item">
                        <div className="loading-spinner" style={{ width: 12, height: 12 }} />
                        <span>{currentAgent}</span>
                    </div>
                )}

                {/* Git Branch (mock) */}
                <div className="status-item">
                    <GitBranch size={14} />
                    <span>main</span>
                </div>

                {/* Problems (mock) */}
                <div className="status-item">
                    <X size={14} />
                    <span>0</span>
                    <AlertTriangle size={14} style={{ marginLeft: 4 }} />
                    <span>0</span>
                </div>
            </div>

            <div className="status-bar-right">
                {/* Terminal Toggle */}
                <div
                    className="status-item terminal-toggle"
                    onClick={onToggleTerminal}
                    title="Toggle Terminal (Ctrl+`)"
                >
                    <Terminal size={14} />
                    <span>Terminal</span>
                </div>

                {/* Current File Language */}
                {activeFile && (
                    <div className="status-item">
                        <span>{activeFile.language || 'Plain Text'}</span>
                    </div>
                )}

                {/* Encoding */}
                <div className="status-item">
                    <span>UTF-8</span>
                </div>

                {/* Line Ending */}
                <div className="status-item">
                    <span>LF</span>
                </div>

                {/* Notifications */}
                <div className="status-item">
                    <Bell size={14} />
                </div>
            </div>
        </div>
    );
}

export default StatusBar;
