/**
 * Construct IDE - Main Application
 * 
 * A Cursor-inspired AI Code Editor
 */

import { useState, useEffect, useCallback } from 'react';
import ActivityBar from './components/Layout/ActivityBar';
import Sidebar from './components/Sidebar/Sidebar';
import EditorAreaWithTerminal from './components/Editor/EditorAreaWithTerminal';
import ChatPanel from './components/Chat/ChatPanel';
import StatusBar from './components/Layout/StatusBar';
import { AppProvider } from './context/AppContext';

function App() {
  const [showTerminal, setShowTerminal] = useState(false);

  // Keyboard shortcut for terminal (Ctrl + `)
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.ctrlKey && e.key === '`') {
        e.preventDefault();
        setShowTerminal(prev => !prev);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  return (
    <AppProvider>
      <div className="ide-container">
        <div className="ide-main">
          <ActivityBar />
          <Sidebar />
          <EditorAreaWithTerminal
            showTerminal={showTerminal}
            onToggleTerminal={() => setShowTerminal(!showTerminal)}
            onCloseTerminal={() => setShowTerminal(false)}
          />
          <ChatPanel />
        </div>
        <StatusBar
          showTerminal={showTerminal}
          onToggleTerminal={() => setShowTerminal(!showTerminal)}
        />
      </div>
    </AppProvider>
  );
}

export default App;
