/**
 * Editor Tabs Component
 * 
 * Tab bar for open files
 */

import { useApp } from '../../context/AppContext';
import { X, FileCode, File } from 'lucide-react';

function EditorTabs() {
    const { openFiles, activeFile, setActiveFile, closeFile } = useApp();

    if (openFiles.length === 0) {
        return null;
    }

    const getFileIcon = (filename) => {
        const ext = filename.split('.').pop()?.toLowerCase();

        switch (ext) {
            case 'py':
                return <FileCode size={14} style={{ color: '#3572A5' }} />;
            case 'js':
            case 'jsx':
                return <FileCode size={14} style={{ color: '#f7df1e' }} />;
            case 'ts':
            case 'tsx':
                return <FileCode size={14} style={{ color: '#3178c6' }} />;
            default:
                return <File size={14} />;
        }
    };

    return (
        <div className="editor-tabs">
            {openFiles.map(file => (
                <div
                    key={file.id}
                    className={`editor-tab ${activeFile?.id === file.id ? 'active' : ''}`}
                    onClick={() => setActiveFile(file)}
                >
                    {getFileIcon(file.name)}
                    <span>{file.name}</span>
                    <button
                        className="editor-tab-close"
                        onClick={(e) => {
                            e.stopPropagation();
                            closeFile(file.id);
                        }}
                    >
                        <X size={14} />
                    </button>
                </div>
            ))}
        </div>
    );
}

export default EditorTabs;
