/**
 * Activity Bar Component
 * 
 * Left-side icon strip for navigation
 */

import { useApp } from '../../context/AppContext';
import {
    Files,
    MessageSquare,
    Search,
    Settings,
    GitBranch,
    Sparkles,
    Zap
} from 'lucide-react';

function ActivityBar() {
    const { activePanel, setActivePanel, toggleSidebar, sidebarCollapsed } = useApp();

    const topItems = [
        { id: 'explorer', icon: Files, label: 'Explorer' },
        { id: 'search', icon: Search, label: 'Search' },
        { id: 'chat-history', icon: MessageSquare, label: 'Chat History' },
        { id: 'git', icon: GitBranch, label: 'Source Control' },
    ];

    const bottomItems = [
        { id: 'ai', icon: Sparkles, label: 'AI Features' },
        { id: 'settings', icon: Settings, label: 'Settings' },
    ];

    const handleItemClick = (itemId) => {
        if (activePanel === itemId && !sidebarCollapsed) {
            toggleSidebar();
        } else {
            setActivePanel(itemId);
            if (sidebarCollapsed) {
                toggleSidebar();
            }
        }
    };

    return (
        <div className="activity-bar">
            {/* Logo */}
            <div
                className="activity-item"
                style={{
                    marginBottom: '8px',
                    color: 'var(--accent-primary)',
                }}
                title="Construct IDE"
            >
                <Zap size={24} strokeWidth={2.5} />
            </div>

            {/* Top items */}
            <div className="activity-bar-top">
                {topItems.map(item => (
                    <button
                        key={item.id}
                        className={`activity-item ${activePanel === item.id ? 'active' : ''}`}
                        onClick={() => handleItemClick(item.id)}
                        title={item.label}
                    >
                        <item.icon size={22} />
                    </button>
                ))}
            </div>

            {/* Bottom items */}
            <div className="activity-bar-bottom">
                {bottomItems.map(item => (
                    <button
                        key={item.id}
                        className={`activity-item ${activePanel === item.id ? 'active' : ''}`}
                        onClick={() => handleItemClick(item.id)}
                        title={item.label}
                    >
                        <item.icon size={22} />
                    </button>
                ))}
            </div>
        </div>
    );
}

export default ActivityBar;
