/**
 * Chat Panel Component
 * 
 * AI chat interface with streaming support
 */

import { useState, useRef, useEffect } from 'react';
import { useApp } from '../../context/AppContext';
import {
    Sparkles,
    Send,
    Plus,
    Trash2,
    Copy,
    Check,
    ChevronRight,
    ChevronLeft,
    Bot,
    User,
    Zap
} from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

function ChatPanel() {
    const {
        messages,
        isStreaming,
        currentAgent,
        chatPanelCollapsed,
        toggleChatPanel,
        sendMessage,
        clearChat,
        newChat,
        connectionStatus
    } = useApp();

    const [input, setInput] = useState('');
    const messagesEndRef = useRef(null);
    const inputRef = useRef(null);

    // Auto-scroll to bottom
    useEffect(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    }, [messages, isStreaming]);

    const handleSubmit = (e) => {
        e.preventDefault();
        if (input.trim() && !isStreaming) {
            sendMessage(input.trim());
            setInput('');
        }
    };

    const handleKeyDown = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            handleSubmit(e);
        }
    };

    if (chatPanelCollapsed) {
        return (
            <div
                style={{
                    width: '40px',
                    background: 'var(--bg-surface)',
                    borderLeft: '1px solid var(--border-default)',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    paddingTop: '8px',
                }}
            >
                <button
                    className="activity-item"
                    onClick={toggleChatPanel}
                    title="Open AI Chat"
                >
                    <ChevronLeft size={20} />
                </button>
                <button
                    className="activity-item"
                    style={{ marginTop: '4px', color: 'var(--accent-primary)' }}
                    title="AI Chat"
                >
                    <Sparkles size={20} />
                </button>
            </div>
        );
    }

    return (
        <div className="chat-panel">
            {/* Header */}
            <div className="chat-header">
                <div className="chat-header-title">
                    <Sparkles size={16} />
                    AI Assistant
                </div>
                <div className="chat-header-actions">
                    <button
                        className="chat-action-btn"
                        onClick={newChat}
                        title="New Chat"
                    >
                        <Plus size={16} />
                    </button>
                    <button
                        className="chat-action-btn"
                        onClick={clearChat}
                        title="Clear Chat"
                    >
                        <Trash2 size={16} />
                    </button>
                    <button
                        className="chat-action-btn"
                        onClick={toggleChatPanel}
                        title="Collapse"
                    >
                        <ChevronRight size={16} />
                    </button>
                </div>
            </div>

            {/* Messages */}
            <div className="chat-messages">
                {messages.length === 0 ? (
                    <WelcomeMessage />
                ) : (
                    messages.map(msg => (
                        <ChatMessage key={msg.id} message={msg} />
                    ))
                )}

                {/* Streaming indicator */}
                {isStreaming && currentAgent && (
                    <div className="agent-status">
                        <div className="agent-status-dot" />
                        <div className="agent-status-text">
                            <span className="agent-status-name">{currentAgent}</span> is thinking...
                        </div>
                    </div>
                )}

                <div ref={messagesEndRef} />
            </div>

            {/* Input */}
            <div className="chat-input-area">
                <form onSubmit={handleSubmit}>
                    <div className="chat-input-wrapper">
                        <textarea
                            ref={inputRef}
                            className="chat-input"
                            value={input}
                            onChange={(e) => setInput(e.target.value)}
                            onKeyDown={handleKeyDown}
                            placeholder="Ask AI to review your code..."
                            rows={1}
                            disabled={isStreaming}
                        />
                        <button
                            type="submit"
                            className="chat-send-btn"
                            disabled={!input.trim() || isStreaming}
                        >
                            <Send size={16} />
                        </button>
                    </div>
                </form>
                <div className="chat-input-hint">
                    Press Enter to send  Shift+Enter for new line
                </div>
            </div>
        </div>
    );
}

// Welcome message component
function WelcomeMessage() {
    const { sendMessage } = useApp();

    const suggestions = [
        'Review my Python code for bugs',
        'Explain this function to me',
        'How can I optimize this code?',
        'Find security vulnerabilities',
    ];

    return (
        <div style={{ padding: '16px 0' }}>
            <div style={{
                textAlign: 'center',
                marginBottom: '24px',
                padding: '0 16px'
            }}>
                <div style={{
                    width: '48px',
                    height: '48px',
                    borderRadius: '12px',
                    background: 'var(--gradient-accent)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    margin: '0 auto 12px',
                }}>
                    <Zap size={24} color="white" />
                </div>
                <h3 style={{
                    fontSize: '16px',
                    fontWeight: '600',
                    color: 'var(--text-primary)',
                    marginBottom: '4px'
                }}>
                    Construct AI
                </h3>
                <p style={{
                    fontSize: '13px',
                    color: 'var(--text-secondary)',
                    lineHeight: '1.5'
                }}>
                    Your AI-powered code review assistant. Ask me anything about your code!
                </p>
            </div>

            <div style={{ fontSize: '12px', color: 'var(--text-muted)', marginBottom: '8px' }}>
                Try asking:
            </div>

            <div style={{ display: 'flex', flexDirection: 'column', gap: '6px' }}>
                {suggestions.map((suggestion, i) => (
                    <button
                        key={i}
                        className="session-item"
                        style={{
                            textAlign: 'left',
                            padding: '10px 12px',
                            cursor: 'pointer',
                        }}
                        onClick={() => sendMessage(suggestion)}
                    >
                        <div style={{
                            fontSize: '13px',
                            color: 'var(--text-primary)'
                        }}>
                            {suggestion}
                        </div>
                    </button>
                ))}
            </div>
        </div>
    );
}

// Individual chat message component
function ChatMessage({ message }) {
    const [copied, setCopied] = useState(false);
    const isUser = message.role === 'user';

    const copyToClipboard = async (text) => {
        await navigator.clipboard.writeText(text);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };

    // Agent badge color
    const getAgentColor = (sender) => {
        const colors = {
            supervisor: 'supervisor',
            planner: 'planner',
            researcher: 'researcher',
            coder: 'coder',
            reviewer: 'reviewer',
            system: 'supervisor',
        };
        return colors[sender?.toLowerCase()] || 'coder';
    };

    return (
        <div className={`chat-message ${isUser ? 'chat-message-user' : 'chat-message-ai'}`}>
            {!isUser && message.sender && (
                <div className="message-sender">
                    <Bot size={14} />
                    <span className={`agent-badge ${getAgentColor(message.sender)}`}>
                        {message.sender}
                    </span>
                </div>
            )}

            <div className="message-content">
                <div className="message-text">
                    {isUser ? (
                        message.content
                    ) : (
                        <ReactMarkdown
                            remarkPlugins={[remarkGfm]}
                            components={{
                                code: ({ node, inline, className, children, ...props }) => {
                                    const match = /language-(\w+)/.exec(className || '');
                                    const codeString = String(children).replace(/\n$/, '');

                                    if (!inline && match) {
                                        return (
                                            <div style={{ position: 'relative' }}>
                                                <div style={{
                                                    position: 'absolute',
                                                    top: '8px',
                                                    right: '8px',
                                                    display: 'flex',
                                                    gap: '4px',
                                                }}>
                                                    <button
                                                        className="chat-action-btn"
                                                        onClick={() => copyToClipboard(codeString)}
                                                        title="Copy code"
                                                        style={{
                                                            width: '24px',
                                                            height: '24px',
                                                            background: 'var(--bg-overlay)',
                                                        }}
                                                    >
                                                        {copied ? <Check size={12} /> : <Copy size={12} />}
                                                    </button>
                                                </div>
                                                <pre>
                                                    <code className={className} {...props}>
                                                        {children}
                                                    </code>
                                                </pre>
                                            </div>
                                        );
                                    }

                                    return (
                                        <code className={className} {...props}>
                                            {children}
                                        </code>
                                    );
                                }
                            }}
                        >
                            {message.content}
                        </ReactMarkdown>
                    )}
                </div>
            </div>
        </div>
    );
}

export default ChatPanel;
