/**
 * File Explorer Component
 * 
 * Shows empty state - users open their own files (like Cursor)
 */

import { useState } from 'react';
import { useApp } from '../../context/AppContext';
import {
    FolderOpen,
    Plus,
    Upload
} from 'lucide-react';

function FileExplorer() {
    const { openFile } = useApp();

    // Handle opening files from user's system
    const handleOpenFile = () => {
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = '.py,.js,.jsx,.ts,.tsx,.json,.md,.css,.html,.yaml,.yml,.txt';
        input.multiple = true;

        input.onchange = (e) => {
            const files = Array.from(e.target.files);
            files.forEach(file => {
                const reader = new FileReader();
                reader.onload = (event) => {
                    const content = event.target.result;
                    openFile({
                        id: `file-${Date.now()}-${file.name}`,
                        name: file.name,
                        path: `/${file.name}`,
                        language: getLanguageFromName(file.name),
                        content: content,
                    });
                };
                reader.readAsText(file);
            });
        };

        input.click();
    };

    const getLanguageFromName = (filename) => {
        const ext = filename.split('.').pop()?.toLowerCase();
        const langMap = {
            'py': 'python',
            'js': 'javascript',
            'jsx': 'javascript',
            'ts': 'typescript',
            'tsx': 'typescript',
            'json': 'json',
            'md': 'markdown',
            'css': 'css',
            'html': 'html',
        };
        return langMap[ext] || 'plaintext';
    };

    return (
        <div className="file-tree">
            {/* Empty State - Open Folder */}
            <div className="empty-state" style={{ padding: '32px 16px' }}>
                <FolderOpen
                    className="empty-state-icon"
                    style={{
                        width: 40,
                        height: 40,
                        marginBottom: 16,
                        color: 'var(--text-muted)'
                    }}
                />
                <div className="empty-state-title" style={{ marginBottom: 8 }}>
                    No folder open
                </div>
                <div className="empty-state-description" style={{ marginBottom: 16, fontSize: 12 }}>
                    Open files to start editing
                </div>

                <button
                    className="btn btn-secondary"
                    onClick={handleOpenFile}
                    style={{
                        width: '100%',
                        padding: '10px 16px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        gap: 8,
                        fontSize: 13,
                        background: 'var(--bg-overlay)',
                        border: '1px solid var(--border-default)',
                        borderRadius: 6,
                        cursor: 'pointer',
                        color: 'var(--text-primary)',
                        transition: 'all 0.15s ease'
                    }}
                >
                    <Upload size={16} />
                    Open Files
                </button>
            </div>
        </div>
    );
}

export default FileExplorer;
