/**
 * Sidebar Component
 * 
 * Collapsible sidebar with file explorer and chat history
 */

import { useApp } from '../../context/AppContext';
import FileExplorer from './FileExplorer';
import ChatHistory from './ChatHistory';
import { Search, Settings, Sparkles } from 'lucide-react';

function Sidebar() {
    const { activePanel, sidebarCollapsed } = useApp();

    const getPanelTitle = () => {
        switch (activePanel) {
            case 'explorer':
                return 'Explorer';
            case 'search':
                return 'Search';
            case 'chat-history':
                return 'Chat History';
            case 'git':
                return 'Source Control';
            case 'ai':
                return 'AI Features';
            case 'settings':
                return 'Settings';
            default:
                return 'Explorer';
        }
    };

    const renderContent = () => {
        switch (activePanel) {
            case 'explorer':
                return <FileExplorer />;
            case 'chat-history':
                return <ChatHistory />;
            case 'search':
                return <SearchPanel />;
            case 'ai':
                return <AIFeaturesPanel />;
            case 'settings':
                return <SettingsPanel />;
            default:
                return <FileExplorer />;
        }
    };

    return (
        <div className={`sidebar ${sidebarCollapsed ? 'collapsed' : ''}`}>
            <div className="sidebar-header">
                <span>{getPanelTitle()}</span>
            </div>
            <div className="sidebar-content">
                {renderContent()}
            </div>
        </div>
    );
}

// Search Panel
function SearchPanel() {
    return (
        <div>
            <div style={{ marginBottom: '12px' }}>
                <div className="chat-input-wrapper" style={{ marginBottom: '8px' }}>
                    <input
                        type="text"
                        className="chat-input"
                        placeholder="Search files..."
                        style={{ minHeight: '36px', paddingRight: '12px' }}
                    />
                </div>
            </div>
            <div className="empty-state">
                <Search className="empty-state-icon" />
                <div className="empty-state-title">Search Files</div>
                <div className="empty-state-description">
                    Type to search across all files in the workspace
                </div>
            </div>
        </div>
    );
}

// AI Features Panel
function AIFeaturesPanel() {
    return (
        <div>
            <div className="empty-state">
                <Sparkles className="empty-state-icon" />
                <div className="empty-state-title">AI Features</div>
                <div className="empty-state-description">
                    Access AI-powered code review, refactoring, and more
                </div>
            </div>

            <div style={{ padding: '0 8px' }}>
                <div className="session-item" style={{ marginBottom: '8px' }}>
                    <div className="session-item-title"> Code Review</div>
                    <div className="session-item-meta">Analyze code for issues and improvements</div>
                </div>
                <div className="session-item" style={{ marginBottom: '8px' }}>
                    <div className="session-item-title"> Refactor</div>
                    <div className="session-item-meta">AI-powered code refactoring suggestions</div>
                </div>
                <div className="session-item" style={{ marginBottom: '8px' }}>
                    <div className="session-item-title"> Document</div>
                    <div className="session-item-meta">Generate documentation for your code</div>
                </div>
                <div className="session-item">
                    <div className="session-item-title"> Debug</div>
                    <div className="session-item-meta">Find and fix bugs in your code</div>
                </div>
            </div>
        </div>
    );
}

// Settings Panel
function SettingsPanel() {
    return (
        <div>
            <div className="empty-state">
                <Settings className="empty-state-icon" />
                <div className="empty-state-title">Settings</div>
                <div className="empty-state-description">
                    Configure your IDE preferences
                </div>
            </div>

            <div style={{ padding: '0 8px' }}>
                <div className="session-item" style={{ marginBottom: '8px' }}>
                    <div className="session-item-title">Theme</div>
                    <div className="session-item-meta">Dark (Default)</div>
                </div>
                <div className="session-item" style={{ marginBottom: '8px' }}>
                    <div className="session-item-title">Font Size</div>
                    <div className="session-item-meta">14px</div>
                </div>
                <div className="session-item">
                    <div className="session-item-title">Backend URL</div>
                    <div className="session-item-meta" style={{ fontSize: '10px' }}>
                        {import.meta.env.VITE_BACKEND_URL || 'http://localhost:8000'}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Sidebar;
