/**
 * Monaco Editor Component
 * 
 * Code editor with syntax highlighting
 */

import { useRef, useCallback } from 'react';
import Editor from '@monaco-editor/react';
import { useApp } from '../../context/AppContext';

// Language mapping
const getLanguage = (filename) => {
    const ext = filename.split('.').pop()?.toLowerCase();

    const languageMap = {
        'py': 'python',
        'js': 'javascript',
        'jsx': 'javascript',
        'ts': 'typescript',
        'tsx': 'typescript',
        'json': 'json',
        'md': 'markdown',
        'css': 'css',
        'html': 'html',
        'yaml': 'yaml',
        'yml': 'yaml',
        'sh': 'shell',
        'bash': 'shell',
        'txt': 'plaintext',
    };

    return languageMap[ext] || 'plaintext';
};

// Monaco editor theme - Ultra dark black/grey (Cursor-style)
const constructTheme = {
    base: 'vs-dark',
    inherit: true,
    rules: [
        { token: '', foreground: 'e5e5e5', background: '000000' },
        { token: 'comment', foreground: '525252', fontStyle: 'italic' },
        { token: 'keyword', foreground: 'a3a3a3' },
        { token: 'string', foreground: '86efac' },
        { token: 'number', foreground: '93c5fd' },
        { token: 'type', foreground: 'fcd34d' },
        { token: 'function', foreground: 'c4b5fd' },
        { token: 'variable', foreground: 'e5e5e5' },
        { token: 'constant', foreground: '93c5fd' },
        { token: 'class', foreground: 'fcd34d' },
        { token: 'interface', foreground: 'fcd34d' },
        { token: 'namespace', foreground: 'fcd34d' },
        { token: 'operator', foreground: 'a3a3a3' },
        { token: 'delimiter', foreground: '737373' },
        { token: 'bracket', foreground: '737373' },
    ],
    colors: {
        'editor.background': '#000000',
        'editor.foreground': '#e5e5e5',
        'editor.lineHighlightBackground': '#0a0a0a',
        'editor.selectionBackground': '#262626',
        'editor.inactiveSelectionBackground': '#1a1a1a',
        'editorCursor.foreground': '#e5e5e5',
        'editorLineNumber.foreground': '#404040',
        'editorLineNumber.activeForeground': '#737373',
        'editorIndentGuide.background': '#1a1a1a',
        'editorIndentGuide.activeBackground': '#262626',
        'editor.selectionHighlightBackground': '#26262633',
        'editorBracketMatch.background': '#26262655',
        'editorBracketMatch.border': '#404040',
        'scrollbarSlider.background': '#26262680',
        'scrollbarSlider.hoverBackground': '#404040',
        'scrollbarSlider.activeBackground': '#525252',
        'minimap.background': '#000000',
        'minimap.selectionHighlight': '#262626',
        'editorWidget.background': '#0a0a0a',
        'editorWidget.border': '#262626',
        'editorSuggestWidget.background': '#0a0a0a',
        'editorSuggestWidget.border': '#262626',
        'editorHoverWidget.background': '#0a0a0a',
        'editorHoverWidget.border': '#262626',
    }
};

function MonacoEditor({ file }) {
    const editorRef = useRef(null);
    const { sendMessage } = useApp();

    const handleEditorDidMount = useCallback((editor, monaco) => {
        editorRef.current = editor;

        // Define custom theme
        monaco.editor.defineTheme('construct', constructTheme);
        monaco.editor.setTheme('construct');

        // Add keyboard shortcut for AI
        editor.addCommand(monaco.KeyMod.CtrlCmd | monaco.KeyCode.KeyL, () => {
            // Focus chat input
            const chatInput = document.querySelector('.chat-input');
            if (chatInput) {
                chatInput.focus();
            }
        });

        // Add context menu action to send to AI
        editor.addAction({
            id: 'send-to-ai',
            label: 'Send to AI Chat',
            keybindings: [monaco.KeyMod.CtrlCmd | monaco.KeyMod.Shift | monaco.KeyCode.KeyL],
            contextMenuGroupId: 'navigation',
            contextMenuOrder: 1.5,
            run: (ed) => {
                const selection = ed.getSelection();
                const selectedText = ed.getModel().getValueInRange(selection);
                if (selectedText) {
                    sendMessage(`Review this code:\n\`\`\`${getLanguage(file.name)}\n${selectedText}\n\`\`\``);
                }
            }
        });
    }, [file.name, sendMessage]);

    const handleEditorChange = useCallback((value) => {
        // Could save to state or trigger auto-save
        console.log('Editor content changed');
    }, []);

    return (
        <Editor
            height="100%"
            language={getLanguage(file.name)}
            value={file.content}
            theme="construct"
            onChange={handleEditorChange}
            onMount={handleEditorDidMount}
            options={{
                fontSize: 14,
                fontFamily: "'JetBrains Mono', 'Fira Code', Consolas, monospace",
                fontLigatures: true,
                lineNumbers: 'on',
                minimap: {
                    enabled: true,
                    scale: 1,
                    showSlider: 'mouseover'
                },
                scrollBeyondLastLine: false,
                automaticLayout: true,
                tabSize: 4,
                insertSpaces: true,
                wordWrap: 'off',
                renderWhitespace: 'selection',
                cursorBlinking: 'smooth',
                cursorSmoothCaretAnimation: 'on',
                smoothScrolling: true,
                padding: { top: 16, bottom: 16 },
                folding: true,
                foldingHighlight: true,
                showFoldingControls: 'mouseover',
                bracketPairColorization: { enabled: true },
                guides: {
                    bracketPairs: true,
                    indentation: true,
                },
                suggest: {
                    showMethods: true,
                    showFunctions: true,
                    showConstructors: true,
                    showFields: true,
                    showVariables: true,
                    showClasses: true,
                    showStructs: true,
                    showInterfaces: true,
                    showModules: true,
                    showProperties: true,
                    showEvents: true,
                    showOperators: true,
                    showUnits: true,
                    showValues: true,
                    showConstants: true,
                    showEnums: true,
                    showEnumMembers: true,
                    showKeywords: true,
                    showWords: true,
                    showColors: true,
                    showFiles: true,
                    showReferences: true,
                    showFolders: true,
                    showTypeParameters: true,
                    showSnippets: true,
                }
            }}
            loading={
                <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    height: '100%',
                    background: 'var(--bg-base)'
                }}>
                    <div className="loading-spinner" />
                </div>
            }
        />
    );
}

export default MonacoEditor;
