/**
 * Application Context
 * 
 * Global state management for the Construct IDE
 */

import { createContext, useContext, useState, useCallback, useEffect, useRef } from 'react';

// Backend configuration
const BACKEND_URL = import.meta.env.VITE_BACKEND_URL || 'http://localhost:8000';
const WS_URL = BACKEND_URL.replace('http', 'ws') + '/api/v1/ws';

// Initial state
const initialState = {
    // UI State
    activePanel: 'explorer', // 'explorer' | 'chat-history' | 'search' | 'settings'
    sidebarCollapsed: false,
    chatPanelCollapsed: false,
    theme: 'dark',

    // Editor State
    openFiles: [],
    activeFile: null,

    // Chat State
    messages: [],
    isStreaming: false,
    currentAgent: null,
    sessionId: null,
    sessions: [],

    // Connection State
    connectionStatus: 'disconnected', // 'connected' | 'disconnected' | 'connecting'
};

// Create context
const AppContext = createContext(null);

// Provider component
export function AppProvider({ children }) {
    const [state, setState] = useState(initialState);
    const wsRef = useRef(null);
    const reconnectTimeoutRef = useRef(null);

    // Update state helper
    const updateState = useCallback((updates) => {
        setState(prev => ({ ...prev, ...updates }));
    }, []);

    // WebSocket connection
    const connectWebSocket = useCallback(() => {
        if (wsRef.current?.readyState === WebSocket.OPEN) return;

        updateState({ connectionStatus: 'connecting' });

        try {
            wsRef.current = new WebSocket(WS_URL);

            wsRef.current.onopen = () => {
                console.log(' WebSocket connected');
                updateState({ connectionStatus: 'connected' });
            };

            wsRef.current.onclose = () => {
                console.log(' WebSocket disconnected');
                updateState({ connectionStatus: 'disconnected' });

                // Reconnect after 3 seconds
                reconnectTimeoutRef.current = setTimeout(connectWebSocket, 3000);
            };

            wsRef.current.onerror = (error) => {
                console.error('WebSocket error:', error);
            };

            wsRef.current.onmessage = (event) => {
                try {
                    const data = JSON.parse(event.data);
                    handleWebSocketMessage(data);
                } catch (e) {
                    console.error('Failed to parse WebSocket message:', e);
                }
            };
        } catch (error) {
            console.error('WebSocket connection error:', error);
            updateState({ connectionStatus: 'disconnected' });
        }
    }, [updateState]);

    // Handle incoming WebSocket messages
    const handleWebSocketMessage = useCallback((data) => {
        switch (data.type) {
            case 'token':
                // Add or update the current AI message
                setState(prev => {
                    const messages = [...prev.messages];
                    const lastMsg = messages[messages.length - 1];

                    if (lastMsg && lastMsg.role === 'assistant' && lastMsg.sender === data.sender) {
                        // Append to existing message
                        lastMsg.content += data.content;
                        return { ...prev, messages, currentAgent: data.sender };
                    } else {
                        // Create new message
                        return {
                            ...prev,
                            messages: [...messages, {
                                id: Date.now().toString(),
                                role: 'assistant',
                                content: data.content,
                                sender: data.sender,
                                timestamp: new Date().toISOString(),
                            }],
                            currentAgent: data.sender,
                            isStreaming: true,
                        };
                    }
                });
                break;

            case 'complete':
                updateState({ isStreaming: false, currentAgent: null });
                break;

            case 'error':
                setState(prev => ({
                    ...prev,
                    messages: [...prev.messages, {
                        id: Date.now().toString(),
                        role: 'assistant',
                        content: `Error: ${data.error}\n${data.details || ''}`,
                        sender: 'system',
                        isError: true,
                        timestamp: new Date().toISOString(),
                    }],
                    isStreaming: false,
                    currentAgent: null,
                }));
                break;
        }
    }, [updateState]);

    // Send message
    const sendMessage = useCallback((message) => {
        if (!message.trim()) return;

        // Add user message
        setState(prev => ({
            ...prev,
            messages: [...prev.messages, {
                id: Date.now().toString(),
                role: 'user',
                content: message,
                timestamp: new Date().toISOString(),
            }],
            isStreaming: true,
        }));

        // Send via WebSocket
        if (wsRef.current?.readyState === WebSocket.OPEN) {
            wsRef.current.send(message);
        } else {
            // Simulate AI response if not connected
            setTimeout(() => {
                setState(prev => ({
                    ...prev,
                    messages: [...prev.messages, {
                        id: Date.now().toString(),
                        role: 'assistant',
                        content: ' Not connected to backend. Please ensure the backend server is running at ' + BACKEND_URL,
                        sender: 'system',
                        timestamp: new Date().toISOString(),
                    }],
                    isStreaming: false,
                }));
            }, 500);
        }
    }, []);

    // File operations
    const openFile = useCallback((file) => {
        setState(prev => {
            const isAlreadyOpen = prev.openFiles.some(f => f.id === file.id);
            return {
                ...prev,
                openFiles: isAlreadyOpen ? prev.openFiles : [...prev.openFiles, file],
                activeFile: file,
            };
        });
    }, []);

    const closeFile = useCallback((fileId) => {
        setState(prev => {
            const newOpenFiles = prev.openFiles.filter(f => f.id !== fileId);
            let newActiveFile = prev.activeFile;

            if (prev.activeFile?.id === fileId) {
                newActiveFile = newOpenFiles[newOpenFiles.length - 1] || null;
            }

            return {
                ...prev,
                openFiles: newOpenFiles,
                activeFile: newActiveFile,
            };
        });
    }, []);

    const setActiveFile = useCallback((file) => {
        updateState({ activeFile: file });
    }, [updateState]);

    // Panel operations
    const setActivePanel = useCallback((panel) => {
        updateState({ activePanel: panel });
    }, [updateState]);

    const toggleSidebar = useCallback(() => {
        setState(prev => ({ ...prev, sidebarCollapsed: !prev.sidebarCollapsed }));
    }, []);

    const toggleChatPanel = useCallback(() => {
        setState(prev => ({ ...prev, chatPanelCollapsed: !prev.chatPanelCollapsed }));
    }, []);

    // Clear chat
    const clearChat = useCallback(() => {
        updateState({ messages: [], sessionId: null });
    }, [updateState]);

    // New chat
    const newChat = useCallback(() => {
        updateState({
            messages: [],
            sessionId: `session-${Date.now()}`,
            currentAgent: null,
            isStreaming: false,
        });
    }, [updateState]);

    // Connect WebSocket on mount
    useEffect(() => {
        connectWebSocket();

        return () => {
            if (reconnectTimeoutRef.current) {
                clearTimeout(reconnectTimeoutRef.current);
            }
            if (wsRef.current) {
                wsRef.current.close();
            }
        };
    }, [connectWebSocket]);

    // Context value
    const value = {
        ...state,

        // Actions
        sendMessage,
        openFile,
        closeFile,
        setActiveFile,
        setActivePanel,
        toggleSidebar,
        toggleChatPanel,
        clearChat,
        newChat,
        connectWebSocket,
    };

    return (
        <AppContext.Provider value={value}>
            {children}
        </AppContext.Provider>
    );
}

// Custom hook
export function useApp() {
    const context = useContext(AppContext);
    if (!context) {
        throw new Error('useApp must be used within an AppProvider');
    }
    return context;
}

export default AppContext;
